import React from 'react'
import { Link } from 'react-router-dom'



export default function Naavbar(props) {
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-transparent">
  <div className="container-fluid">
    <a className="navbar-brand" href="#">voomo</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    
    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
         
            {
              props.UserData ? 
              <>
               <li className="nav-item">
          <Link className="nav-link " aria-current="page" to="home" >Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="about">about</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="movie">movie</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="network">network</Link>
        </li>
      
              </> : ''
            }
        
           
           
            
    
     

</ul> 
      
 
      <ul className='navbar-nav me-3 mb-2 mb-lg-0 ms-auto'>
        <li className='nav-item d-flex align-items-center'>
        <i class="fa-brands fa-instagram  mx-2"></i>
        <i class="fa-brands fa-facebook  mx-2"></i>       
        <i class="fa-brands fa-youtube  mx-2"></i>     
           <i className='fab mx-2 fa-twitter' ></i>
        </li>
               
                  
        {
      props.UserData ? 
      <>
<li claasName="nav-item">
          <span className="nav-link" onClick={props.LogOut}  >Logout</span>
        </li>
      
      </>: 
                    <>
              <li className="nav-item">
              <Link className="nav-link" to="login">Login</Link>
            </li>
            <li claasName="nav-item">
              <Link className="nav-link" to="register">Register</Link>
            </li>
            

      </>
    
    }
      
                
        
           
      
      
      </ul>
    
    </div>
  </div>
</nav>

    )
}
