import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';

export default function ActorDetails() {
    let baseImageUrl  = 'https://image.tmdb.org/t/p/original/';
const [ActorDetails, setActorDetails] = useState({});
let parm =  useParams();
async function getActorDetails() 
{
    let {data} = await axios.get(`https://api.themoviedb.org/3/person/${parm.id}?api_key=962235938e93a7ebd52595b7579413dd&language=e`)
    setActorDetails(data);
    console.log(data);
}
useEffect(() => 
{
    getActorDetails();
},[]);



  return <> 
     <div className='row'>
              
        <div>
            
        </div>



             <div className='col-md-4 my-5' >
            <img className='w-100 mb-3' src={ baseImageUrl+ActorDetails .profile_path} alt=""/>
           </div>
           <div className='col-md-8 my-5' >
               <h2>   Name:  {ActorDetails.name}  </h2>


               <p className='my-3'>    Known For Department : {ActorDetails.known_for_department}   </p>

               <p className='my-3'> Biography :   {ActorDetails.biography}  </p>
           </div> 
            
     </div>
     </>
}
