import axios from 'axios'
import React, { useEffect , useState } from 'react'
import { Link } from 'react-router-dom';
import './home.css'
export default function Home() {
 
  let baseImageUrl  = 'https://image.tmdb.org/t/p/original/';
const [trendingMovie, setTrendingMovie] = useState([]);
const [trendingTvshow, setTrendingTvshows] = useState([]);
const [trendingPerson, setTrendingPerson] = useState([]);
async function getTrendingData(mediaType,callback){
let {data} =   await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=962235938e93a7ebd52595b7579413dd`); 
callback(data.results);
}
useEffect(() =>
{
  getTrendingData("movie",setTrendingMovie);
  getTrendingData("tv",setTrendingTvshows);
  getTrendingData("person",setTrendingPerson);
},[]);
  return <>
          
          <div className='row'> 
          <div className='col-md-4 d-flex align-items-center'>
        <div className='w-100'>
        <div className='brdr'></div>
      
      <h2>Trending</h2>
      <h2>Movie</h2>
      <h2>To watch now</h2>
      
      <p className='second-co'>Most watched movies by day</p>
      <div className='second-brdr'></div>
        </div>
          </div>
            {trendingMovie.map((movie,index)=>            
            <div className='col-md-2 my-5' key={index}>
              <div>
                 <Link to={`/MovieDetails/${movie.id}`}> 
                 <img className='w-100 mb-3' src={baseImageUrl+movie.poster_path} alt=""/>
                 
              <h4> {movie.title} </h4>

                 
                  </Link>
              </div>
            </div>)}
          </div>
           <div className='row'> 
           <div className='col-md-4 d-flex align-items-center'>
         <div className='w-100'>
         <div className='brdr'></div>
       
       <h2>Trending</h2>
       <h2>tv</h2>
       <h2>To watch now</h2>
       
       <p className='second-co'>Most watched tv by day</p>
       <div className='second-brdr'></div>
         </div>
           </div>
             {trendingTvshow.map((tv,index)=>            
             <div className='col-md-2 my-5' key={index}>
               <div>
               <Link to={`/TvShowDetails/${tv.id}`}>
                <img className='w-100 mb-3' src={baseImageUrl+tv.poster_path} alt=""/>
               <h4> {tv.name} </h4>
               </Link>
 
               </div>
             </div>)}
           </div>
           <div className='row'> 
           <div className='col-md-4 d-flex align-items-center'>
         <div className='w-100'>
         <div className='brdr'></div>
       
       <h2>Trending</h2>
       <h2>person</h2>
       
       <p className='second-co'>Most watched person by day</p>
       <div className='second-brdr'></div>
         </div>
           </div>
             {trendingPerson.map((person,index)=>            
             <div className='col-md-2 my-5' key={index}>
               <div>
               <Link to={`/ActorDetails/${person.id}`}>
                <img className='w-100 mb-3' src={baseImageUrl+person.profile_path} alt=""/>
               <h4> {person.name} </h4>
               </Link>
 
               </div>
             </div>)}
           </div>

           </>
}
