import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import './movieDetails.css'

export default function MovieDetails() {
    let baseImageUrl  = 'https://image.tmdb.org/t/p/original/';
const [MovieDetails, setMovieDetails] = useState({});
let parm =  useParams();
async function getMovieDetails() 
{
    let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${parm.id}?api_key=962235938e93a7ebd52595b7579413dd&language=e`)
    setMovieDetails(data);
    console.log(data);
}
useEffect(() => 
{
    getMovieDetails();
},[]);



  return <> 
     <div className='row'>
              
        



             <div className='col-md-4 my-5' >
            <img className='w-100 mb-3' src={ baseImageUrl+ MovieDetails.poster_path} alt=""/>
           </div>
           <div className='col-md-8 my-5' >
               <h2>    {MovieDetails.original_title}  </h2>
                 
             {/* <button  type="button" className="btn btn-secondary ">{MovieDetails.genres[0].name}</button>
            <button type="button" className="btn btn-secondary my-5"> {MovieDetails.genres[1].name}</button>
            <button type="button" className="btn btn-secondary my-5">{MovieDetails.genres[2].name}</button>
  */}

             <p className='my-3'>   Release Date :  {MovieDetails.release_date} </p>
               <p className='my-3'>    Rate : {MovieDetails.vote_average}   </p>

               <p className='my-3'> Overview :   {MovieDetails.overview}  </p>
           </div> 
            
     </div>
     </>
}
