import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import './register.css'
export default function Register() {
  const [errorList, setErrorList] = useState([])
  const [isLoading, setIsLoading] = useState(false)
const [error, setError] = useState('')
let navigate = useNavigate();
const [user,setUser]=useState(
{
first_name:"",
last_name:"",
email:"",
password:"",
age:"",
}
)
function getUser(e)
{
let myUser = {...user};
myUser[e.target.name] = e.target.value;
setUser(myUser);
}

 async function submit_Register(e)
{
  e.preventDefault();
  setIsLoading(true)
  let validateResult = validateRegister(user);
if(validateResult.error){
  setIsLoading(false); 
  setErrorList(validateResult.error.details)
}
else{

  let {data} = await axios.post('https://route-egypt-api.herokuapp.com/signup' , user )
  
  if(data.message === 'success')
  {
  setIsLoading(false);
  navigate('../login');

  }
  else
  {
  setError(data.message)
  setIsLoading(false)
  }
  }
}

function validateRegister()
{
  let schema = Joi.object({
    first_name:Joi.string().alphanum().min(3).max(8).required(),
    last_name:Joi.string().alphanum().min(3).max(8).required(),
     age:Joi.number().min(16).max(60).required(),
     email:Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }),
    password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')),
    })
    return schema.validate(user , {abortEarly:false})
} 

return (
<div className='Register_form'>
    <h2 className='my-5'> Register Form</h2>
    {errorList.map((error , index) => 
    {
      if(index===4)
      {
        return <div key={index} className="alert alert-danger ">
        password invalid
        </div>  
      }
      else{
       return  <div key={index} className="alert alert-danger ">
        {error.message}
        </div> 
      }
    }
    )}
    
      
   
{error? <div className='alert alert-danger'>
        {error}
      </div> : ''}

    <form onSubmit={submit_Register}>
      
            <label htmlFor="first_name">first_name </label>
      <input onChange={getUser} type="text" name="first_name" className='form-control my-3' id='first_name'/>
           
      <label htmlFor="last_name">last_name </label>
      <input onChange={getUser} type="text" name="last_name" className='form-control my-3' id='last_name'/>
      <label htmlFor="age">age </label>
      <input onChange={getUser} type="numyer" name="age" className='form-control my-3' id='age'/>

      <label htmlFor="email">email </label>
      <input onChange={getUser} type="email" name="email" className='form-control my-3' id='email'/>

      <label htmlFor="password">password </label>
      <input onChange={getUser} type="password" name="password" className='form-control my-3' id='password'/>

      
 <button className='btn btn-outline-info my-3 '>
 {isLoading? <i className='fas fa-spinner fa-spin' ></i> : "Register" }  </button>
    
        </form>
    </div>
  )
}
