
import { useEffect, useState } from 'react';
import jwtDecode from 'jwt-decode';
import {  Navigate, Route,  Routes, useNavigate } from 'react-router-dom';
import './App.css';
import Login from './components/login/Login';
import Naavbar from './components/Navbar/Naavbar';
import Register from './components/register/register';
import MovieDetails from './components/MovieDetails/MovieDetails';
import About from './pages/about/About';
import Home from './pages/Home/Home';
import Movie from './pages/movie/Movie';
import Network from './pages/network/Network';
import TvShowDetails from './components/MovieDetails/tvshowDetails/TvshowDetails';
import ActorDetails from './components/actors/Actor';
function App() {
 

 const [UserData, setUserData] = useState(null); 
  
let navigate =  useNavigate();
useEffect(()=> 
{
if(localStorage.getItem('userToken'))
{
  getUserData();
}

},[] )
 function getUserData()
 {
   let decodeToken = jwtDecode(localStorage.getItem('userToken'));
   setUserData(decodeToken);
  } 
  useEffect(() => {console.log(UserData)} , [UserData])
 
 function LogOut()
 {
   localStorage.removeItem('userToken')
   setUserData(null);
   navigate('/login');
 }
 function ProtectedRoute( {children} )
{
  if (!localStorage.getItem('userToken')) {
  return  <Navigate to='/login'/>
    
  }
  else
  {
        return children
  }
}

  return (

   
    <div >
       <Naavbar UserData={UserData}  LogOut={LogOut} />

       <div className='container'>
    <Routes>
    <Route path='/' element={<Home/>} />
      <Route path='Home' element={  <ProtectedRoute>  <Home />  </ProtectedRoute>     } />
      <Route path='about' element={   <ProtectedRoute>       <About />    </ProtectedRoute>     }          />
      <Route path='movie' element={    <ProtectedRoute>  <Movie />      </ProtectedRoute>    }    />
      <Route path='MovieDetails' element={    <ProtectedRoute>  < MovieDetails/>      </ProtectedRoute>    }>    
    <Route path=':id' element={ < MovieDetails/>  } />
       </Route>
       <Route path='TvShowDetails' element={    <ProtectedRoute>  < TvShowDetails/>      </ProtectedRoute>    }>    
    <Route path=':id' element={ < TvShowDetails/>  } />
       </Route>
       <Route path='ActorDetails' element={    <ProtectedRoute>  < ActorDetails/>      </ProtectedRoute>    }>    
    <Route path=':id' element={ < ActorDetails/>  } />
       </Route>

        
        
      <Route path='network' element={  <ProtectedRoute> <Network /> </ProtectedRoute>} />


      <Route path='login' element={   <Login getUserData={getUserData}/>  } />
      <Route path='register' element={    <Register />   }/>
      <Route path='*' element={<h2>404</h2>} />
    </Routes>
    </div>
    </div>
  );
}

export default App;
