import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';

export default function TvShowDetails() {
    let baseImageUrl  = 'https://image.tmdb.org/t/p/original/';
const [TvShowDetails, setTvShowDetails] = useState({});
let parm =  useParams();
async function getTvShowDetails() 
{
    let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${parm.id}?api_key=962235938e93a7ebd52595b7579413dd&language=e`)
    setTvShowDetails(data);
    console.log(data);
}
useEffect(() => 
{
    getTvShowDetails();
},[]);



  return <> 
     <div className='row'>
              
        <div>
            
        </div>



             <div className='col-md-4 my-5' >
            <img className='w-100 mb-3' src={ baseImageUrl+ TvShowDetails.poster_path} alt=""/>
           </div>
           <div className='col-md-8 my-5' >
               <h2>    {TvShowDetails.original_title}  </h2>
{/*                  
             <button  type="button" className="btn btn-secondary ">{MovieDetails.genres[0].name}</button>
            <button type="button" className="btn btn-secondary my-5"> {MovieDetails.genres[1].name}</button>
            <button type="button" className="btn btn-secondary my-5">{MovieDetails.genres[2].name}</button>
  */}

             <p className='my-3'>   Release Date :  {TvShowDetails.last_air_date  || TvShowDetails.first_air_date }  </p>
               <p className='my-3'>    Vote Average : {TvShowDetails.vote_average}   </p>

               <p className='my-3'> Overview :   {TvShowDetails.overview}  </p>
           </div> 
            
     </div>
     </>
}
